#app4{

}

@keyframes change{
    0%{
        background: red;
    }
    100%{
        background: blue;
    }
}

#app4 .circle{
    border: 1px solid red;
    width: 25vw;
    height: 25vw;
    border-radius: 50%;
}
#app4 .circle.active{
    animation: change 1s infinite alternate linear;
}
